import { Component, OnInit, Injector, ViewChild, HostListener } from '@angular/core';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDialog } from '@angular/material/dialog';
import { FacilityApproveConfirmationModalComponent } from '../facility-approve-confirmation-modal/facility-approve-confirmation-modal.component';
import { Router, ActivatedRoute } from '@angular/router';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-facility-booking-actions',
  templateUrl: './facility-booking-actions.component.html',
  styleUrls: ['./facility-booking-actions.component.scss']
})
export class FacilityBookingActionsComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  gridSourceData:any;
  columnData:any;
  bookingListData:any;
  bookingSearch:string;
  facilityListData = [];
  statusTypeData = [];
  filterFacility:any = {};
  modalService:any;
  urlType:string;
  isBookingDataLoaded:boolean = false;
  isAdminLogin:boolean = false;

  constructor(
    private injector: Injector,
    private facilityService:FacilityService,
    private cookieService:CookieService,
    private lookupService: LookupService,
    private router: Router,
    private activeRouter: ActivatedRoute,
    public dialog: MatDialog,
    private sharedService:SharedService,
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  @HostListener('window:onStatusFacilityBooking', ['$event.detail'])
  changeStatus(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    const dialogRef = this.dialog.open(FacilityApproveConfirmationModalComponent, {
      width: '600px',
      height:'auto',
      data:dataRecord
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        //this.getWorkApprovalList();
      }
    });
  }

  @HostListener('window:onEditFacilityBooking', ['$event.detail'])
  editBooking(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.router.navigate(['ams/facility/edit-booking',dataRecord.apartmentFacilityBookingId]);
  }

  @HostListener('window:ondeleteMeeting', ['$event.detail'])
  ondeleteSlot(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal(dataRecord.apartmentFacilityBookingId);
  }

  isAdmin(){
    this.isAdminLogin = (this.cookieService.get('userRoleType') == 'Admin' || this.cookieService.get('userRoleType') == 'Staff') ? true : false;
  }

  resetField() {
    this.filterFacility = {
      facilityBookingStatusID:'',
      eventName:'',
      apartmentFacilityID:'',
      bookedOn:''
    }
    this.getBookingList();
  }

  searchData() {
    if(this.bookingSearch != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.bookingSearch;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'facilitybooking');
  }

  getBookingList() {
    this.isBookingDataLoaded = false;
    let bookingListParams:any = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      eventName: this.filterFacility.eventName,
      apartmentFacilityID: this.filterFacility.apartmentFacilityID,
      bookedOn: this.filterFacility.bookedOn
    }
    if(this.urlType == 'all-bookings') {
      bookingListParams.facilityBookingStatusID = this.filterFacility.facilityBookingStatusID;
    } else if(this.urlType == 'bookings-confirmed'){
      bookingListParams.facilityBookingStatusID = 188;
    } else if(this.urlType == 'bookings-pending'){
      bookingListParams.facilityBookingStatusID = 189;
    } else {
      bookingListParams.facilityBookingStatusID = 385;
    }
    this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingListParams).subscribe((res:any) => {
	    this.bookingListData = res.filter(data => {
	      return data.isActive
      });
      this.gridSourceData = {
        localdata: this.bookingListData.reverse(),
        datatype: "array"
      }
      this.bookingListData = new jqx.dataAdapter(this.gridSourceData);
	    this.isBookingDataLoaded = true;
    })
  }

  ngOnInit() {
    this.activeRouter.url.subscribe((data)=>{
      this.urlType = data[0].path;
    })
    this.getBookingList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
      text: 'Facility Name',
      datafield: 'apartmentFacilityName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Event Date',
      datafield: 'bookedForDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Event Time',
      datafield: 'bookedFromTime',
        cellsrenderer: (row, column, value) => {
          let time,fromTime,toTime = this.bookingListData.loadedData[row].bookedToTime;
          if(value && toTime)  {
          fromTime = moment(value,'HH:mm:ss').format('hh:mm A');
          toTime = moment(toTime,'HH:mm:ss').format('hh:mm A');
          time = `${fromTime} - ${toTime}`
          } else {
            time = '-'
          }
        return '<div class="jqx-custom-inner-cell">'+ time +'</div>'
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 160,
      renderer: columnrenderer
    },{
      text: 'Total Hours',
      datafield: 'bookedToTime',
      cellsrenderer: (row, column, value) => {
        let time,toTime,fromTime = this.bookingListData.loadedData[row].bookedFromTime;
        if(fromTime && value) {
          fromTime = moment(fromTime,'HH:mm:ss').format();
          toTime = moment(value,'HH:mm:ss').format();
          time = moment.duration(moment(toTime).diff(moment(fromTime)));
          if(time.minutes() == 0) {
            time = `${time.hours()}hrs`
          } else {
            time = `${time.hours()}hrs ${time.minutes()}mins`
          }
          if(time == '23hrs 58mins') {
            time = '24hrs'
          }
        } else {
          time = '-'
        }
        return '<div class="jqx-custom-inner-cell ml-4">'+ time +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Rate Type',
      datafield: 'rateBaseIdName',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Cost',
      datafield: 'amount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Discount',
      datafield: 'discount',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Final Bill',
      datafield: 'totalBillAmount',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Booked For',
      datafield: 'isBookingforGuest',
      cellsrenderer: (row, column, value) => {
        value =  value ? 'Guest' : 'Unit User';
        return '<div class="jqx-custom-inner-cell">'+ value +'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'Status',
      datafield: 'facilityBookingStatusId',
      cellsrenderer: (row, column, value) => {
        let label,className;
        label = this.bookingListData.loadedData[row].facilityBookingStatusId_Label;
        className  = label == 'Rejected' ? 'cancelled' : label.toLowerCase();
        return '<div class="jqx-custom-inner-cell"><span onClick="statusFacilityBooking('+ row +')" class="badge small min text-capitalize link '+ className +'">'+ label +'</span></div>'
      },
      cellsalign: 'center', 
      align: 'center',
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions">'
        +'<a href="javascript:void(0)" class="mr-3" onClick="editFacilityBooking('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
        +'<a href="javascript:void(0)" class="mr-2" onClick="showConfirmDelete(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
      },
      minwidth: 80,
      renderer: columnrenderer
    }];
    //facility List
    let categoryParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(categoryParams).subscribe((res:any) => {
      this.facilityListData = res.filter(item => {
        return item.isActive;
      });
    })
    //facility status
    if(this.urlType == 'all-bookings') {
      let statusParams = {
        LookupTypeId: 40
      }
      this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res:any) => {
        this.statusTypeData = res.filter( item => {
          return item.isActive;
        });
      });
    }
    //delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( item => {
      if(item != null) {
        var params = {
          apartmentFacilityBookingId: item,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }
        this.facilityService.deleteApartmentFacilityBooking(params).subscribe((res:any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.getBookingList();
        });
      }
    });
  }
}

function statusFacilityBooking(row){
  var event = new CustomEvent('onStatusFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).statusFacilityBooking = statusFacilityBooking;


function editFacilityBooking(row){
  var event = new CustomEvent('onEditFacilityBooking', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editFacilityBooking = editFacilityBooking;

function showConfirmDelete(row){
  var event = new CustomEvent('ondeleteMeeting', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDelete = showConfirmDelete;
