import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { FacilityComponent } from './facility.component';
import { FacilityReportsComponent } from './components/facility-reports/facility-reports.component';
import { FacilityCreateBookingComponent } from '../../shared/components/facility-create-booking/facility-create-booking.component';
import { FacilityBookingActionsComponent } from './components/facility-booking-actions/facility-booking-actions.component';
import { FacilityCalenderComponent } from './components/facility-calender/facility-calender.component';

import { FacilityListComponent } from './components/facility-list/facility-list.component';
import { FacilityCreateComponent } from './components/facility-create/facility-create.component';

const routes: Routes = [
	{ path: '', redirectTo: 'list', pathMatch: 'full'},
	{ path: 'list', component: FacilityListComponent},
	{ path: 'setup', component: FacilityCreateComponent},
	{ path: 'create-booking', component: FacilityCreateBookingComponent},
	{ path: 'edit-booking/:id', component: FacilityCreateBookingComponent},
	{ path: 'view-facility-calendar', component: FacilityCalenderComponent},
	{ path: 'all-bookings', component: FacilityBookingActionsComponent},
	{ path: 'bookings-pending', component: FacilityBookingActionsComponent},
	{ path: 'bookings-confirmed', component: FacilityBookingActionsComponent},
	{ path: 'bookings-cancelled', component: FacilityBookingActionsComponent},
	{ path: 'reports', component: FacilityReportsComponent },
	{ path: '**', redirectTo: 'list', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityRoutingModule { }
