import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { FacilityService } from 'src/app/api/controllers/Facility';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { FacilityServiceService } from '../../service/facility-service.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { Router } from '@angular/router';

@Component({
  selector: 'app-facility-list',
  templateUrl: './facility-list.component.html',
  styleUrls: ['./facility-list.component.scss']
})
export class FacilityListComponent implements OnInit {
  isFacilityCategoryLoaded:boolean = false;
  facilityCategoryData:any
  pesoValue:string;

  constructor(
    private facilityService:FacilityService,
    private facilitylocalservice:FacilityServiceService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private router:Router
  ) {}

  addNewFacilityCategory() {
    this.router.navigate(['/ams/facility/setup'],{queryParams:{type:'create'}});
  }

  viewFacility(data) {
    this.router.navigate(['/ams/facility/setup'],{queryParams:{type:'view',id:data.apartmentFacilityId}});
  }

  updateFacility(data) {
    this.router.navigate(['/ams/facility/setup'],{queryParams:{type:'edit',id:data.apartmentFacilityId}});
  }

  addSlotFacility(data) {
    this.router.navigate(['/ams/facility/setup'],{queryParams:{type:'slot',id:data.apartmentFacilityId}});
  }

  getRateBase(id){
    if(id == 138){
      return 'Hour';
    }
    else {
      return 'Day';
    }
  }


  getSlots(items) {
    var slots = items.filter( item => {
      return item.isActive;
    });
    return slots;
  }

  ngOnInit() {
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(params).subscribe((res:any) => {
    this.facilityCategoryData = res.filter(item => {
        return item.isActive;
      })
      this.isFacilityCategoryLoaded = true;
    },
    error => {
      console.log(error);
    }); 

    let entity = {
      LookupTypeId: 101,
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
    }
    this.lookupService.getLookupValueByLookupTypeId(entity).subscribe((res:any) => {
      this.pesoValue = res[0].lookupValueName;
  	});
  }

}
