import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { jqxSchedulerComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxscheduler';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';

@Component({
  selector: 'app-facility-calender',
  templateUrl: './facility-calender.component.html',
  styleUrls: ['./facility-calender.component.scss']
})
export class FacilityCalenderComponent implements OnInit,AfterViewInit {
  @ViewChild('scheduler', { static: false }) myScheduler: jqxSchedulerComponent;
  facilitySlotData:any;
  date: any = new jqx.date(new Date());
  dataAdapter: any;
  source: any =
  {
    dataType: 'array',
    dataFields: [
        { name: 'id', type: 'string' },
        { name: 'description', type: 'string' },
        { name: 'location', type: 'string' },
        { name: 'subject', type: 'string' },
        { name: 'calendar', type: 'string' },
        { name: 'start', type: 'date' },
        { name: 'end', type: 'date' }
    ],
    id: 'id',
  };
  resources: any =
  {
    colorScheme: 'scheme04',
    dataField: 'calendar',
    orientation: 'vertical',
    //source: new jqx.dataAdapter(this.source)
  };
  appointmentDataFields: any = {
    from: 'start',
    to: 'end',
    id: 'id',
    description: 'description',
    location: 'place',
    subject: 'subject',
    resourceId: 'calendar'
  };
  views: any[] =
  [
    { type: 'timelineDayView', appointmentHeight: 10 },
    { type: 'timelineWeekView', appointmentHeight: 10 },
    { type: 'timelineMonthView', appointmentHeight: 10 }
  ];

  constructor(
    private facilityService:FacilityService,
    private cookieService:CookieService
    ) {}

  getWidth() : any {
    if (document.body.offsetWidth < 850) {
      return '90%';
    }
    return 1000;
  }

  ngOnInit() {
    let bookingParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      facilityBookingStatusID: 188,
    }
    this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingParams).subscribe((res:any) => {
      let facilityBookingData = res.filter(item => {
       return item.isActive;
      });
      let calenderData = [];
      facilityBookingData.forEach((booking)=>{
        let year  = moment(booking.bookedForDate).year();
        let month  = moment(booking.bookedForDate).month();
        let date  = moment(booking.bookedForDate).date();
        let day = moment(booking.bookedForDate).day();
        let startHours  = moment(booking.bookedFromTime,'HH:mm:ss').hour();
        let startMin  = moment(booking.bookedFromTime,'HH:mm:ss').minute();
        let endHours  = moment(booking.bookedToTime, 'HH:mm:ss').hour();
        let endMin  = moment(booking.bookedToTime, 'HH:mm:ss').minute();
        let entity = {
          id: booking.apartmentFacilityBookingId,
          description: '',
          location: '',
          subject:  `${booking.apartmentFacilityName} ${startHours}:${startMin} - ${endHours}:${endMin}`,
          calendar: booking.apartmentFacilityName,
          start: new Date(year, month, date, startHours, startMin),
          end: new Date(year, month, date, endHours, endMin)
        }
        calenderData.push(entity);
      })
      this.source.localdata = calenderData;
      this.dataAdapter = new jqx.dataAdapter(this.source);
      this.resources.source = new jqx.dataAdapter(this.source);
    })
  }

  ngAfterViewInit() {
    this.myScheduler.ensureAppointmentVisible('id2');
  }
}
